html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
}

a {
  text-decoration: none;
}
a:active, a:hover {
  outline: 0;
}

ul,
li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus {
  outline: 0;
}

img,
audio,
video {
  max-width: 100%;
  height: auto;
}

audio,
canvas,
iframe,
video,
img,
svg {
  vertical-align: middle;
}

iframe {
  border: 0;
}

textarea {
  resize: none; /*remove the resize handle on the bottom right*/
  overflow: auto;
  vertical-align: top;
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
}

input,
textarea,
select,
button {
  outline: none;
  border: none;
  font-size: 100%;
  margin: 0;
}

button,
input {
  line-height: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
  text-align: left;
}

.clear {
  clear: both;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

html, body {
  font-family: "Noto Sans", sans-serif;
  line-height: 140%;
  padding: 0;
  margin: 0;
  font-weight: 400;
  color: rgb(255, 250, 250);
  position: relative;
}

body {
  font-size: 16px;
  background-color: rgb(248, 248, 248);
}
body.overflow {
  overflow-y: hidden;
}

* {
  box-sizing: border-box;
}

.container {
  max-width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  font-size: 16px;
  position: relative;
}

section {
  width: 100%;
}

.task-list {
  padding-top: 20px;
  background-color: #1F2428;
  height: calc(100vh - 120px);
}

.header {
  height: 60px;
  background-color: #24292E;
  width: 100%;
  border-bottom: 1px solid #1B1F23;
}
.header .container {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__ico {
  cursor: pointer;
  min-width: 36px;
  max-width: 36px;
}
.header__ico img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.header__profile {
  position: relative;
}
.header__auth {
  border-radius: 4px;
  background-color: rgb(255, 250, 250);
  position: absolute;
  z-index: 10;
  top: 100%;
  padding: 20px 12px;
  left: -120px;
  width: 120px;
  display: none;
}
.header__auth.is-active {
  display: block;
}
.header__btn {
  cursor: pointer;
  margin-bottom: 12px;
  padding: 8px 12px;
  color: rgb(255, 250, 250);
  border-radius: 4px;
  width: 100%;
  transition: 0.25s;
}
.header__btn--profile {
  background-color: #9457EB;
}
.header__btn--profile:hover {
  background-color: #a66df7;
}
.header__btn--out {
  background-color: #FF5252;
}
.header__btn--out:hover {
  background-color: #fa6b6b;
}
.header__btn:last-child {
  margin-bottom: 0;
}

.footer {
  background-color: #24292E;
  height: 60px;
  border-top: 1px solid #1B1F23;
}